*{
font-family: 'Courier New', Courier, monospace;
background-color: bisque;
}

body {
  padding: 60px;
}

input{
    font-size: 1.3rem;
    width: 250px;
    color: gray;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 1px;
  overflow: hidden;
  margin-bottom: 5px;  
  margin-right: 5px;
}

.card p {
padding: 20px;
text-align: center;
}

.card img {
  max-height: 500px;
}